import Link from 'next/link';
import { request } from 'graphql-request';

const Post = props => (
  <div>
    <h1>Test</h1>
    <ul>
      {props.route.sailings.map(sailing => (
        <li>{sailing.vessel}</li>
      ))}
    </ul>
  </div>
);

Post.getInitialProps = async function(context) {
  console.log(context.query);
  const routeName = context.query.id;
  const gqlq = `{
      route(routeName: "${routeName}"){
          sailings {
              vessel
          }
      }
  }`;
  const res = await request('https://server-dubymzzcjd.now.sh/graphql', gqlq);
  console.log(`Route info fetched. Sailings: ${res.route.sailings.length}`);
  return res;
};

export default Post;



